*,
*::before,
*::after {
  box-sizing: border-box;
}

img {
  display: block;
}

* {
  padding: 0;
  margin: 0;
}

ul {
  list-style: none;
}

.is-hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
  transform: translateX(-50%) translateY(-50%) scale(0) rotate(0deg) skew(40deg, 40deg);
}

.is-hidden .buy-now-modal-body {
  scale: 0;
}

.modal:not(.is-hidden) .buy-now-modal-body {
  scale: 1;
}

.buy-now-modal {
  position: fixed;
  inset: 0;
  background-color: rgb(0 0 0 / 0.5);
  opacity: 1;
  pointer-events: all;
  visibility: visible;
  transition: opacity 250ms;
  margin: 0 auto;
  overflow: auto;
}

.buy-now-modal_body {
  position: relative;
  max-width: 280px;
  margin: auto;
  background-color: #fcfcfc;
  border: 2px solid #f8d24e;
  border-radius: 10px;
  top: 60px;
}

@media screen and (min-width: 768px) {
  .buy-now-modal_body {
    max-width: 568px;
  }
}

.buy-now-modal_body h3 {
  padding-inline: 74px;
  text-align: center;
  margin-top: 28px;
  font-family: Seymour One;
  font-size: 2rem;
  color: #131313;
  font-weight: bold;
}

@media screen and (min-width: 768px) {
  .buy-now-modal_body h3 {
    margin-top: 32px;
  }
}

.buy-now-checkbox1 {
  opacity: 0;
  width: 132px;
  height: 199px;
  position: absolute;
  transform: translate(0, -62px);
}

.buy-now-li-body1 {
  width: 130px;
  height: 121px;
  position: absolute;
  transform: translate(-1px, 15px);
  border-radius: 15px;
  cursor: pointer;
}

.buy-now-checkbox1:checked + .buy-now-li-body1 {
