.buy-now-paragraph {
    position: absolute;
    top: 96px;
    left: 13px;
    font-weight: 700;
    font-size: 16;
    line-height: 1.25;
    letter-spacing: 0.04em;
    color: #131313;
}

.buy-now-properties {
    width: 224px;
    height: 32px;
    border: 1px solid var(--main-bg-color);
    background: #F8D24E1A;
    border-radius: 10px;
    margin-inline: 28px;
    padding-left: 12px;
    color: var(--dark-font-color);
    font-size: 14;
    line-height:1.4;
}

.buy-now-textarea {
    resize: none;
    width: 224px;
    height: 65px;
    border: 1px solid var(--main-bg-color);
    background: #F8D24E1A;
    border-radius: 10px;
    margin-inline: 28px;
    padding-left: 12px;
    color: var(--dark-font-color);
    font-size: 14;
    font-family: Montserrat;
    line-height: 1.4;
    padding-top: 11px;
}

@media screen and (min-width:768px) {
    .buy-now-properties {
        width: 368px;
        height: 42px;
        margin-inline: 100px;
        padding-left: 16px;
    }
}

@media screen and (min-width:768px) {
    .buy-now-textarea {
        width: 368px;        
        margin-inline: 100px;
        height: 84px;
        padding-left: 16px;
    }
}


.buy-now-properties:focus-within,
.buy-now-textarea {
  outline: none;
}

.buy-now-inputs-wrapper {
  margin-top: 26px;
  display: flex;
  flex-direction: column;
  gap: 6px;
}

@media screen and (min-width: 768px) {
  .buy-now-inputs-wrapper {
    margin-top: 32px;
    gap: 8px;
  }
}

.buy-now-button-wrapper {
  display: flex;
  justify-content: center;
  margin-top: 26px;
  margin-bottom: 29px;
}

@media screen and (min-width: 768px) {
  .buy-now-button-wrapper {
    margin-top: 32px;
    margin-bottom: 32px;
  }
}

.buy-now-button {
  background-color: #f8d24e;
  padding: 12px 34px;
  border: none;
  border-radius: 10px;
  font-family: Montserrat;
  font-weight: 600;
  font-size: 14;
  line-height: 1.3;
  color: #131313;
}

.buy-now-properties:not(:placeholder-shown):not(:focus):invalid {
  outline: 2px solid red;
  outline-offset: 1px;
}

.buy-now-modal-close {
  position: absolute;
  top: 12px;
  right: 12px;
  border: none;
  background-color: inherit;
  cursor: pointer;
}
